.section-contents {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    max-width: 768px;
    width: 100%;
}

.section-name {
    margin: 0;
    font-weight: 700;
}

.subsection-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    height: 2rem;
    position: relative;
    width: 100%;
}

.subsection-contents {
    position: absolute;
    font-size: 1.5rem;
    color: #555;
    transition: all 0.4s ease;
    font-weight: 700
}

@media (min-width: 768px) {
    .section-contents {
	align-items: flex-start;	
	box-sizing: border-box;
	margin-right: auto;
	padding-left: 6rem;
    }

    .subsection-container {
	display: block;
    }
    
    .subsection-contents {
    }
}
